//* Global Variables
$background-color: rgb(205, 244, 253);
$secondary-background: rgba(233, 207, 151, 0.7);
$font-color: black;
$link-color: black;
$primary-color: #555;
$highlight-1: #75ffff;
$highlight-2: orange;
$highlight-3: red;
$font-size: 1.1rem;
$header-size: 1.7rem;
$font-text: "Roboto Slab", serif;
$font-header: "Mochiy Pop P One", sans-serif;
$menu-speed: 0.75s;

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: $font-text;
  font-size: $font-size;
  background-color: $background-color;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
nav,
ul {
  margin: 0;
  padding: 0;
}

h2 {
  font-size: ($header-size * 1.2);
  padding-bottom: 1.5rem;
}
h3 {
  font-size: ($header-size * 1);
}

img {
  width: 100%;
  height: auto;
  object-fit: contain;
  border-radius: 10px;
}

a {
  display: block;
  color: $link-color;
  text-decoration: none;
}

ul {
  list-style-type: none;
}

p {
  text-align: justify;
  font-size: $font-size;
  line-height: 1.5;
}

.container {
  margin: 0 auto;
  padding: 1.5rem 2rem 0 2rem;
}

.inner-container {
  margin: 0 -15px;
}

//* Universal CSS Properities
.button {
  background-color: burlywood;
  padding: 0.5rem 1rem;
  border: 1px solid black;
  border-radius: 10px;
  transition: box-shadow 0.4s ease;

  &:hover {
    box-shadow: -5px -5px $highlight-2;
    transition: box-shadow 0.4s;
  }
}

.line {
  min-height: 1.5px;
  width: auto;
  background-color: $highlight-1;
  margin: 4rem 0;
  opacity: 0.3;
}

.twobytwo {
  &:first-child {
    grid-area: one;
  }
  &:nth-child(2) {
    grid-area: two;
  }
  &:nth-child(3) {
    grid-area: three;
  }
  &:nth-child(4) {
    grid-area: four;
  }

  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-template-rows: repeat(2, minmax(0, 1fr));
  gap: 1rem;
  grid-template-areas:
    "one two"
    "three four";
  height: 100%;
  width: 100%;
}
// ****************************************
// ** Navigation **************************
// ****************************************
//! Apply Hamburger drop down menu out here

//! Hamburger

.menu-wrap {
  // hamburger
  display: flex;
  flex-flow: row wrap;
  #head {
    margin-right: auto;
    h2 {
      font-size: 1.8rem;
    }
  }
  #head,
  .ham-container {
    flex-basis: auto;
  }
  .ham-container {
    text-align: right;
    z-index: 1000;
    .mobile {
      cursor: pointer;
      height: 45px;
      width: 45px;
      font-size: 1.8rem;
      background-color: transparent;
      border: none;
    }
    // rotate hamburger
    .rotated > span {
      transform: rotate(90deg);
    }
    span {
      transition: all 0.4s ease;
    }
  }
  // hide navigation
  .nav-container {
    flex-basis: 100%;
  }
  .hidden > nav {
    height: 0;
    transform: scale(0);
  }
  nav {
    height: 160px;
    transition: all 0.4s ease;
    overflow: hidden;
    li {
      text-align: right;
      &:last-child > a {
        padding-right: 0.25rem;
        @media (max-width: 768px) {
          margin-left: 0.25rem;
        }
        &:hover {
          color: inherit;
        }
      }

      a {
        padding: 0.25rem 0;
        transition: color 0.4s ease;
        &:hover {
          color: $highlight-2;
        }
      }
    }
  }
}

// ****************************************
// ** Header ******************************
// ****************************************
#header {
  height: 700px;
  display: flex;
  align-items: center;
}

.header-container {
  padding-top: 1.5rem;
  .button-container {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    gap: 3%;
    padding-bottom: 1.5rem;
  }
  h1 {
    font-size: calc($header-size * 1.7);
  }
  p {
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    padding-bottom: 0;
  }
}

// ****************************************
// ** Welcome *****************************
// ****************************************
p {
  padding-bottom: 1.5rem;
}
.distribution-container {
  margin-top: 1.5rem;
}

// ****************************************
// ** About us ****************************
// ****************************************
#about-us {
  div > div > p {
    margin-bottom: 0;
    &:last-child {
      padding-bottom: 0;
    }
  }
}

// ****************************************
// ** Specs *******************************
// ****************************************
#specs .specs-container {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-template-rows: 1fr repeat(2, minmax(0, auto));
  grid-template-areas:
    "text text"
    "fruits fruits"
    "fruit fruit";

  gap: 1rem;
  .fruits {
    grid-area: fruits;
    .fruits-inner {
      width: 50%;
      margin: 0 auto;
    }
  }
  .fruit-images {
    grid-area: fruit;
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-template-rows: repeat(2, minmax(0, auto));
    grid-template-areas:
      "one two"
      "three four";
    gap: 1rem;

    // 2b2 nft
    &img:nth-child(1) {
      grid-area: one;
    }
    // Fruits
    &img:nth-child(2) {
      grid-area: two;
    }
    &img:nth-child(3) {
      grid-area: three;
    }
    &img:nth-child(4) {
      grid-area: four;
    }
  }

  .specs-text {
    grid-area: text;
  }
}

// ****************************************
// ** Specs *******************************
// ****************************************

.how-we-work {
  div > p {
    margin-bottom: 0;
    &:last-child {
      padding: 0;
    }
  }
}

// ****************************************
// ** BUY NFT BLOCK ***********************
// ****************************************
.outer {
  background-color: rgba($color: $highlight-3, $alpha: 0.5);
  height: auto;
  padding: 1rem;
  .inner {
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    gap: 4%;
    p {
      order: 3;
      content: "";
      flex-basis: 100%;
      padding: 1rem 0;
      text-align: center;
      margin: 0;
    }
    h3 {
      font-size: calc($header-size * 0.8);
      padding-bottom: 1rem;
      text-align: center;
    }
    .buy-btn {
      justify-self: center;
    }
  }
}
// ****************************************
// ** Footer ******************************
// ****************************************

.time-line-container {
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.timeline {
  width: 80%;
  height: auto;
  max-width: 800px;
  margin: 0 auto;
  position: relative;
  ul {
    list-style: none;
    display: flex;
    flex-flow: column wrap;
  }
  ul li {
    min-height: 200px;
    padding: 20px;
    background-color: $secondary-background;
    color: $font-color;
    border-radius: 10px;
    margin-bottom: 1rem;
    &:last-child {
      margin-bottom: 0;
    }
  }

  .timeline-content {
    h3 {
      font-size: 25px;
      font-weight: 500;
      line-height: 1.2;
      margin-bottom: 10px;
    }
    h4 {
      margin-bottom: 10px;
    }
    p {
      line-height: 1.5;
      font-weight: 300;
    }
  }
}

// ****************************************
// ** Footer ******************************
// ****************************************
#footer .footerContainer {
  display: flex;
  flex-flow: row wrap;
  margin-bottom: 2.5rem;
  justify-content: space-around;

  .logo {
    flex-basis: 100%;
  }
  .terms ul li {
    margin-bottom: 0.5rem;
  }
  .terms ul li:first-child a {
    border-bottom: solid $font-color 1px;
    transition: border-bottom 200ms;
    display: inline;
    &:hover {
      transition: border 200ms;
      border-bottom: solid transparent;
      color: $highlight-2;
      transition: color 200ms;
    }
  }

  [class*="fa-"] {
    transition: color 200ms;
    &:hover {
      color: $highlight-2;
      transition: color 200ms;
    }
  }

  .socials {
    flex-basis: 50%;
    order: 2;
    ul {
      display: flex;
      flex-flow: row nowrap;
      justify-content: end;

      li {
        padding: 0 1rem;
        font-size: 2.5rem;
      }
      li:last-child {
        padding-right: 0;
      }
    }
  }

  .terms ul {
    order: 1;
    flex-basis: 50%;
  }
  .socials,
  .terms {
    padding-top: 1.5rem;
  }
}

// ****************************************
// ** Media Queries ***********************
// ****************************************

@media only screen and (min-width: 578px) {
  .container {
    max-width: 540px;
    padding: 1.5rem 1.5rem 0 1.5rem;
  }
  // * BUYNFT
  #buyNFT .outer {
    padding: 1.5rem;
    gap: 4%;
    .inner {
      flex-flow: row wrap;
      h3 {
        order: 1;
        flex-basis: 48%;
        padding: 0;
      }
      p {
        order: 3;
        text-align: center;
      }

      a {
        order: 2;
        flex-basis: 48%;
        text-align: center;
      }
    }
  }

  // * Footer
  #footer .footerContainer {
    display: grid;
    grid-template-columns: repeat(3, minmax(0 1fr));
    grid-template-rows: repeat(2, minmax(0, 1fr));
    grid-template-areas:
      "logo logo socials"
      "logo logo terms";
    .logo {
      grid-area: logo;
      width: 168px;
    }
    .socials {
      grid-area: socials;
      align-self: end;
      ul {
        justify-content: start;
        li:first-child {
          padding-left: 0;
        }
      }
    }
    .terms {
      grid-area: terms;
    }
  }
}

@media only screen and (min-width: 768px) {
  .container {
    max-width: 750px;
    // position: relative;
  }
  // * Navigation
  #nav-bar {
    height: 120px;
    .menu-wrap {
      display: flex;
      flex-flow: row nowrap;
      justify-content: flex-end;
      align-items: center;

      #head {
        justify-self: flex-start;
        margin-right: auto;
        h2 {
          padding: 0;
          font-size: ($header-size * 1.2);
        }
      }

      .ham-container {
        display: none;
      }
      .nav-container {
        flex-basis: auto;
      }
      .nav-container > nav {
        transform: none;
        height: auto;
        width: 100%;
        overflow: visible;
        > ul {
          display: flex;
          flex-flow: row nowrap;
          justify-content: flex-end;
          align-items: center;
          li {
            padding: 0 1rem;
            font-size: 1.2rem;
            &:last-child {
              padding-right: 0;
            }
          }
        }
      }
      .main-mint {
        padding: 0.25rem 0.35rem;
      }
    }
  }
  // * Header
  #header {
    height: 725px;
  }
  //* BUYNFT
  #buyNFT .outer {
    .inner {
      flex-flow: row nowrap;
      gap: 2%;
      h3 {
        order: 1;
      }
      p {
        order: 2;
        flex-basis: 36%;
      }
      a {
        order: 3;
      }
      a,
      h3 {
        flex-basis: 30%;
      }
    }
  }

  // * Specs
  #specs .specs-container {
    grid-template-columns: repeat(5, minmax(0, 1fr));
    grid-template-rows: repeat(2, minmax(0, auto));
    grid-template-areas:
      "text text text text text"
      "fruits fruit fruit fruit fruit";
    .specs-text {
      p {
        margin: 0;
      }
    }

    .fruit-images {
      grid-template-columns: repeat(4, minmax(0, 1fr));
      grid-template-rows: repeat(1, minmax(0, auto));
      grid-template-areas: "one two three four";
    }
    .fruits .fruits-inner {
      margin: 0;
      width: 100%;
    }
  }

  //* Timeline
  // Create center bar
  .timeline::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 2px;
    left: 50%;
    transform: translateX(-50%);
    background: linear-gradient(
      0deg,
      $highlight-1 0%,
      rgba(197, 250, 255, 0.7) 100%
    );
    // background-color: $secondary-background;
  }
  .timeline ul li {
    h4 {
      font-size: 1rem;
    }
    width: 50%;
    margin-bottom: 50px;
    &:hover:after {
      background-color: $highlight-3;
    }
    //     Moves Children left
    &:nth-child(odd) {
      padding-right: -10px;
      align-self: flex-start;
      transform: translateX(-35px);
      border-radius: 10px 0 10px 10px;
      //       Position of circles
      &::after {
        right: -35px;
        transform: translate(50%, -50%);
      }
    }
    //     Moves children right
    &:nth-child(even) {
      align-self: flex-end;
      transform: translateX(35px);
      border-radius: 0 10px 10px 10px;
      &::after {
        left: -35px;
        transform: translate(-50%, -50%);
      }
    }
    //     Create the circles
    &::after {
      content: "";
      position: absolute;
      height: 20px;
      width: 20px;
      background-color: $secondary-background;
      border-radius: 50%;
      top: 0;
      transition: background 0.4s;
    }
  }
}

@media only screen and (min-width: 992px) {
  .container {
    max-width: 920px;
    .inner-container {
      margin: 0;
    }
  }

  // * Navigation

  // *Header
  #header {
    height: 550px;
  }
  .header-container {
    display: flex;
    flex-flow: row nowrap;
    gap: 4%;
  }
  .header-container .left {
    flex-basis: 49%;
  }
  .header-container .right {
    flex-basis: 49%;
  }

  // * Welcome
  .welcome-container,
  .distribution-container {
    display: flex;
    flex-flow: row nowrap;
    gap: 3%;
  }
  .welcome-container .distribution-container {
    margin-top: 1.5rem;
  }
  .text,
  .fair {
    flex-basis: 66%;
    padding-right: 1rem;
  }
  .twobytwo,
  .fine-text {
    flex-basis: 30%;
  }
  .fine-text {
    p {
      font-size: 0.8rem;
      margin-top: 0;
    }
  }
  p {
    font-size: 1.1rem;
  }
  h3 {
    font-size: calc($header-size * 0.9);
  }

  // * specs
}

@media only screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
  // * Navigation

  // * accumulation
  .text-container {
    display: grid;
    grid-template-columns: repeat(4 minmax(0, 1fr));
    grid-template-rows: repeat(2 minmax(0, 1fr));
    grid-template-areas:
      "one two"
      "three four";
    p:nth-child(1) {
      grid-area: one;
      margin-right: 5rem;
    }
    p:nth-child(2) {
      grid-area: two;
    }
    p:nth-child(3) {
      grid-area: three;
      margin-right: 5rem;
    }
    p:nth-child(4) {
      grid-area: four;
    }
  }
}
