*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: "Roboto Slab", serif;
  font-size: 1.1rem;
  background-color: #cdf4fd;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
nav,
ul {
  margin: 0;
  padding: 0;
}

h2 {
  font-size: 2.04rem;
  padding-bottom: 1.5rem;
}

h3 {
  font-size: 1.7rem;
}

img {
  width: 100%;
  height: auto;
  object-fit: contain;
  border-radius: 10px;
}

a {
  display: block;
  color: black;
  text-decoration: none;
}

ul {
  list-style-type: none;
}

p {
  text-align: justify;
  font-size: 1.1rem;
  line-height: 1.5;
}

.container {
  margin: 0 auto;
  padding: 1.5rem 2rem 0 2rem;
}

.inner-container {
  margin: 0 -15px;
}

.button {
  background-color: burlywood;
  padding: 0.5rem 1rem;
  border: 1px solid black;
  border-radius: 10px;
  transition: box-shadow 0.4s ease;
}
.button:hover {
  box-shadow: -5px -5px orange;
  transition: box-shadow 0.4s;
}

.line {
  min-height: 1.5px;
  width: auto;
  background-color: #75ffff;
  margin: 4rem 0;
  opacity: 0.3;
}

.twobytwo {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-template-rows: repeat(2, minmax(0, 1fr));
  gap: 1rem;
  grid-template-areas: "one two" "three four";
  height: 100%;
  width: 100%;
}
.twobytwo:first-child {
  grid-area: one;
}
.twobytwo:nth-child(2) {
  grid-area: two;
}
.twobytwo:nth-child(3) {
  grid-area: three;
}
.twobytwo:nth-child(4) {
  grid-area: four;
}

.menu-wrap {
  display: flex;
  flex-flow: row wrap;
}
.menu-wrap #head {
  margin-right: auto;
}
.menu-wrap #head h2 {
  font-size: 1.8rem;
}
.menu-wrap #head,
.menu-wrap .ham-container {
  flex-basis: auto;
}
.menu-wrap .ham-container {
  text-align: right;
  z-index: 1000;
}
.menu-wrap .ham-container .mobile {
  cursor: pointer;
  height: 45px;
  width: 45px;
  font-size: 1.8rem;
  background-color: transparent;
  border: none;
}
.menu-wrap .ham-container .rotated > span {
  transform: rotate(90deg);
}
.menu-wrap .ham-container span {
  transition: all 0.4s ease;
}
.menu-wrap .nav-container {
  flex-basis: 100%;
}
.menu-wrap .hidden > nav {
  height: 0;
  transform: scale(0);
}
.menu-wrap nav {
  height: 160px;
  transition: all 0.4s ease;
  overflow: hidden;
}
.menu-wrap nav li {
  text-align: right;
}
.menu-wrap nav li:last-child > a {
  padding-right: 0.25rem;
}
@media (max-width: 768px) {
  .menu-wrap nav li:last-child > a {
    margin-left: 0.25rem;
  }
}
.menu-wrap nav li:last-child > a:hover {
  color: inherit;
}
.menu-wrap nav li a {
  padding: 0.25rem 0;
  transition: color 0.4s ease;
}
.menu-wrap nav li a:hover {
  color: orange;
}

#header {
  height: 700px;
  display: flex;
  align-items: center;
}

.header-container {
  padding-top: 1.5rem;
}
.header-container .button-container {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  gap: 3%;
  padding-bottom: 1.5rem;
}
.header-container h1 {
  font-size: 2.89rem;
}
.header-container p {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  padding-bottom: 0;
}

p {
  padding-bottom: 1.5rem;
}

.distribution-container {
  margin-top: 1.5rem;
}

#about-us div > div > p {
  margin-bottom: 0;
}
#about-us div > div > p:last-child {
  padding-bottom: 0;
}

#specs .specs-container {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-template-rows: 1fr repeat(2, minmax(0, auto));
  grid-template-areas: "text text" "fruits fruits" "fruit fruit";
  gap: 1rem;
}
#specs .specs-container .fruits {
  grid-area: fruits;
}
#specs .specs-container .fruits .fruits-inner {
  width: 50%;
  margin: 0 auto;
}
#specs .specs-container .fruit-images {
  grid-area: fruit;
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-template-rows: repeat(2, minmax(0, auto));
  grid-template-areas: "one two" "three four";
  gap: 1rem;
}
#specs .specs-container .fruit-imagesimg:nth-child(1) {
  grid-area: one;
}
#specs .specs-container .fruit-imagesimg:nth-child(2) {
  grid-area: two;
}
#specs .specs-container .fruit-imagesimg:nth-child(3) {
  grid-area: three;
}
#specs .specs-container .fruit-imagesimg:nth-child(4) {
  grid-area: four;
}
#specs .specs-container .specs-text {
  grid-area: text;
}

.how-we-work div > p {
  margin-bottom: 0;
}
.how-we-work div > p:last-child {
  padding: 0;
}

.outer {
  background-color: rgba(255, 0, 0, 0.5);
  height: auto;
  padding: 1rem;
}
.outer .inner {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  gap: 4%;
}
.outer .inner p {
  order: 3;
  content: "";
  flex-basis: 100%;
  padding: 1rem 0;
  text-align: center;
  margin: 0;
}
.outer .inner h3 {
  font-size: 1.36rem;
  padding-bottom: 1rem;
  text-align: center;
}
.outer .inner .buy-btn {
  justify-self: center;
}

.time-line-container {
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.timeline {
  width: 80%;
  height: auto;
  max-width: 800px;
  margin: 0 auto;
  position: relative;
}
.timeline ul {
  list-style: none;
  display: flex;
  flex-flow: column wrap;
}
.timeline ul li {
  min-height: 200px;
  padding: 20px;
  background-color: rgba(233, 207, 151, 0.7);
  color: black;
  border-radius: 10px;
  margin-bottom: 1rem;
}
.timeline ul li:last-child {
  margin-bottom: 0;
}
.timeline .timeline-content h3 {
  font-size: 25px;
  font-weight: 500;
  line-height: 1.2;
  margin-bottom: 10px;
}
.timeline .timeline-content h4 {
  margin-bottom: 10px;
}
.timeline .timeline-content p {
  line-height: 1.5;
  font-weight: 300;
}

#footer .footerContainer {
  display: flex;
  flex-flow: row wrap;
  margin-bottom: 2.5rem;
  justify-content: space-around;
}
#footer .footerContainer .logo {
  flex-basis: 100%;
}
#footer .footerContainer .terms ul li {
  margin-bottom: 0.5rem;
}
#footer .footerContainer .terms ul li:first-child a {
  border-bottom: solid black 1px;
  transition: border-bottom 200ms;
  display: inline;
}
#footer .footerContainer .terms ul li:first-child a:hover {
  transition: border 200ms;
  border-bottom: solid transparent;
  color: orange;
  transition: color 200ms;
}
#footer .footerContainer [class*=fa-] {
  transition: color 200ms;
}
#footer .footerContainer [class*=fa-]:hover {
  color: orange;
  transition: color 200ms;
}
#footer .footerContainer .socials {
  flex-basis: 50%;
  order: 2;
}
#footer .footerContainer .socials ul {
  display: flex;
  flex-flow: row nowrap;
  justify-content: end;
}
#footer .footerContainer .socials ul li {
  padding: 0 1rem;
  font-size: 2.5rem;
}
#footer .footerContainer .socials ul li:last-child {
  padding-right: 0;
}
#footer .footerContainer .terms ul {
  order: 1;
  flex-basis: 50%;
}
#footer .footerContainer .socials,
#footer .footerContainer .terms {
  padding-top: 1.5rem;
}

@media only screen and (min-width: 578px) {
  .container {
    max-width: 540px;
    padding: 1.5rem 1.5rem 0 1.5rem;
  }

  #buyNFT .outer {
    padding: 1.5rem;
    gap: 4%;
  }
  #buyNFT .outer .inner {
    flex-flow: row wrap;
  }
  #buyNFT .outer .inner h3 {
    order: 1;
    flex-basis: 48%;
    padding: 0;
  }
  #buyNFT .outer .inner p {
    order: 3;
    text-align: center;
  }
  #buyNFT .outer .inner a {
    order: 2;
    flex-basis: 48%;
    text-align: center;
  }

  #footer .footerContainer {
    display: grid;
    grid-template-columns: repeat(3, minmax(0 1fr));
    grid-template-rows: repeat(2, minmax(0, 1fr));
    grid-template-areas: "logo logo socials" "logo logo terms";
  }
  #footer .footerContainer .logo {
    grid-area: logo;
    width: 168px;
  }
  #footer .footerContainer .socials {
    grid-area: socials;
    align-self: end;
  }
  #footer .footerContainer .socials ul {
    justify-content: start;
  }
  #footer .footerContainer .socials ul li:first-child {
    padding-left: 0;
  }
  #footer .footerContainer .terms {
    grid-area: terms;
  }
}
@media only screen and (min-width: 768px) {
  .container {
    max-width: 750px;
  }

  #nav-bar {
    height: 120px;
  }
  #nav-bar .menu-wrap {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-end;
    align-items: center;
  }
  #nav-bar .menu-wrap #head {
    justify-self: flex-start;
    margin-right: auto;
  }
  #nav-bar .menu-wrap #head h2 {
    padding: 0;
    font-size: 2.04rem;
  }
  #nav-bar .menu-wrap .ham-container {
    display: none;
  }
  #nav-bar .menu-wrap .nav-container {
    flex-basis: auto;
  }
  #nav-bar .menu-wrap .nav-container > nav {
    transform: none;
    height: auto;
    width: 100%;
    overflow: visible;
  }
  #nav-bar .menu-wrap .nav-container > nav > ul {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-end;
    align-items: center;
  }
  #nav-bar .menu-wrap .nav-container > nav > ul li {
    padding: 0 1rem;
    font-size: 1.2rem;
  }
  #nav-bar .menu-wrap .nav-container > nav > ul li:last-child {
    padding-right: 0;
  }
  #nav-bar .menu-wrap .main-mint {
    padding: 0.25rem 0.35rem;
  }

  #header {
    height: 725px;
  }

  #buyNFT .outer .inner {
    flex-flow: row nowrap;
    gap: 2%;
  }
  #buyNFT .outer .inner h3 {
    order: 1;
  }
  #buyNFT .outer .inner p {
    order: 2;
    flex-basis: 36%;
  }
  #buyNFT .outer .inner a {
    order: 3;
  }
  #buyNFT .outer .inner a,
#buyNFT .outer .inner h3 {
    flex-basis: 30%;
  }

  #specs .specs-container {
    grid-template-columns: repeat(5, minmax(0, 1fr));
    grid-template-rows: repeat(2, minmax(0, auto));
    grid-template-areas: "text text text text text" "fruits fruit fruit fruit fruit";
  }
  #specs .specs-container .specs-text p {
    margin: 0;
  }
  #specs .specs-container .fruit-images {
    grid-template-columns: repeat(4, minmax(0, 1fr));
    grid-template-rows: repeat(1, minmax(0, auto));
    grid-template-areas: "one two three four";
  }
  #specs .specs-container .fruits .fruits-inner {
    margin: 0;
    width: 100%;
  }

  .timeline::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 2px;
    left: 50%;
    transform: translateX(-50%);
    background: linear-gradient(0deg, #75ffff 0%, rgba(197, 250, 255, 0.7) 100%);
  }

  .timeline ul li {
    width: 50%;
    margin-bottom: 50px;
  }
  .timeline ul li h4 {
    font-size: 1rem;
  }
  .timeline ul li:hover:after {
    background-color: red;
  }
  .timeline ul li:nth-child(odd) {
    padding-right: -10px;
    align-self: flex-start;
    transform: translateX(-35px);
    border-radius: 10px 0 10px 10px;
  }
  .timeline ul li:nth-child(odd)::after {
    right: -35px;
    transform: translate(50%, -50%);
  }
  .timeline ul li:nth-child(even) {
    align-self: flex-end;
    transform: translateX(35px);
    border-radius: 0 10px 10px 10px;
  }
  .timeline ul li:nth-child(even)::after {
    left: -35px;
    transform: translate(-50%, -50%);
  }
  .timeline ul li::after {
    content: "";
    position: absolute;
    height: 20px;
    width: 20px;
    background-color: rgba(233, 207, 151, 0.7);
    border-radius: 50%;
    top: 0;
    transition: background 0.4s;
  }
}
@media only screen and (min-width: 992px) {
  .container {
    max-width: 920px;
  }
  .container .inner-container {
    margin: 0;
  }

  #header {
    height: 550px;
  }

  .header-container {
    display: flex;
    flex-flow: row nowrap;
    gap: 4%;
  }

  .header-container .left {
    flex-basis: 49%;
  }

  .header-container .right {
    flex-basis: 49%;
  }

  .welcome-container,
.distribution-container {
    display: flex;
    flex-flow: row nowrap;
    gap: 3%;
  }

  .welcome-container .distribution-container {
    margin-top: 1.5rem;
  }

  .text,
.fair {
    flex-basis: 66%;
    padding-right: 1rem;
  }

  .twobytwo,
.fine-text {
    flex-basis: 30%;
  }

  .fine-text p {
    font-size: 0.8rem;
    margin-top: 0;
  }

  p {
    font-size: 1.1rem;
  }

  h3 {
    font-size: 1.53rem;
  }
}
@media only screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }

  .text-container {
    display: grid;
    grid-template-columns: repeat(4 minmax(0, 1fr));
    grid-template-rows: repeat(2 minmax(0, 1fr));
    grid-template-areas: "one two" "three four";
  }
  .text-container p:nth-child(1) {
    grid-area: one;
    margin-right: 5rem;
  }
  .text-container p:nth-child(2) {
    grid-area: two;
  }
  .text-container p:nth-child(3) {
    grid-area: three;
    margin-right: 5rem;
  }
  .text-container p:nth-child(4) {
    grid-area: four;
  }
}

